.hero {
  background-image: url("https://www.startpage.com/av/proxy-image?piurl=https%3A%2F%2Fwallpaperaccess.com%2Ffull%2F1287136.png&sp=1738870882T0c5b6a62b30da27b0ca88fe5439436bc5284dd628cdefd52e982e04d89db6557");
  background-size: cover;
  background-position: center;
  height: 600px;
  display: flex;
  border-radius: 20px;
  align-items: center;
  justify-content: center;
  opacity: 0.9;
}

.hero-content {
  text-align: center;
}

.hero-content h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.hero-content h1 span {
  color: #ff6b00;
}

.hero-content p {
  font-size: 1.5rem;
}

