.anime-details {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(20, 20, 35, 0.85);

  backdrop-filter: blur(8px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 2rem;
}

.anime-details-content {
  background: linear-gradient(to bottom right, rgba(40, 40, 60, 0.9), rgba(20, 20, 35, 0.95));
  padding: 2.5rem;
  border-radius: 16px;
  
  display: flex;
  ;
  
  
  width: 100%;
  max-height: auto;
  
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #fff;
}

.close-button {
  background-color: #ff8eb7;
  color: #fff;
  border: none;
  font-size: 1rem;
  text-transform: uppercase;
  width: 200px;
  height: 50px;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 1.5rem;
  font-weight: 600;
  transition: background-color 0.2s ease;
}

.close-button:hover {
  background-color: #ff6b9e;
}

.anime-details-content h2 {
 position: relative;
  top: 100px;
  left: -60px;
  font-size: 1.8rem;
  font-weight: 300;
  text-align: center;
  color: #fff;
}

.anime-details-content img {
  width: 100%;
  height: 600px;
 
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}

.anime-info {
  text-align: left;
  background: rgba(30, 30, 45, 0.5);
  padding: 1.5rem;
  border-radius: 12px;
  margin-top: 1.9rem;
}

.anime-info p {
  margin-bottom: 1.6rem;
  line-height: 1.6;
  color: rgba(255, 255, 255, 0.9);
}

.anime-info p:last-child {
  margin-bottom: 0;
}

.anime-info strong {
  color: #ff8eb7;
  margin-right: 0.5rem;
}

.anime-details-content::-webkit-scrollbar {
  width: 10px;
}

.anime-details-content::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
}

.anime-details-content::-webkit-scrollbar-thumb {
  background: #ff8eb7;
  border-radius: 4px;
}

.anime-details-content p {
  line-height: 1.8;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 1.5rem;
  position: relative;
  top: 100px;
  left: 10px
  
}

