.anime-card {
  cursor: pointer;
  transition: transform 0.3s ease;
  border: 2px solid #ff6b00;
  border-radius: 15px;
}

.anime-card:hover {
  transform: scale(1.05);
}

.anime-card-image {
  position: relative;
  padding-top: 150%;
  overflow: hidden;
}

.anime-card-image img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.anime-card-title {
  margin-top: 0.5rem;
  font-size: 1rem;
  text-align: center;
}

